.one-course {
    padding: 40px 40px;
    display: flex;
    justify-content: center;

    .container {
        @media screen and (max-width: 992px) {
            margin: 0px;
            padding: 0px !important;
            width: 100%;
        }

        .course-container {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;

            @media screen and (max-width: 992px) {
                flex-direction: column;
                padding-top: 40px;
                padding-bottom: 40px;
            }

            .course-video {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                padding: 0px 10px 0px 0px;

                @media screen and (max-width: 992px) {
                    padding: 0px;
                }

                .video,
                .image {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    @media screen and (max-width: 992px) {
                        min-height: unset;
                    }
                }
            }

            .course-description {
                padding-left: 20px;
                padding-right: 20px;
                display: flex;

                @media screen and (max-width: 992px) {
                    margin-top: 40px;
                    padding: 0px !important;
                    margin-left: 20px !important;
                    margin-right: 20px !important;
                }

                @media screen and (max-width: 576px) {
                    margin-top: 40px;
                    margin-left: -20px !important;
                    margin-right: -20px !important;
                }

                .course {
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
                    max-width: 100%;

                    .innerDetail {
                        color: white;
                        font-size: 30px;
                        font-family: inherit;
                        font-weight: bolder;

                        .tag {
                            color: black;
                            background-color: white;
                            padding: 0px 10px;
                            font-size: 15px;
                            text-align: center;
                            margin-bottom: 10px;
                            max-width: 200px;
                            margin-top: -10px;

                            @media screen and (max-width: 992px) {
                                margin-bottom: 20px;
                                margin-top: 0px;
                            }
                        }

                        .title {
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 1;
                            /* number of lines to show */
                            -webkit-box-orient: vertical;
                            height: 40px;
                            letter-spacing: -0.5px;
                            font-family: sans-serif;

                            @media screen and (max-width: 992px) {
                                -webkit-line-clamp: 3 !important;
                                height: auto;
                            }
                        }

                        .description {
                            overflow: hidden;
                            margin-top: 15px;
                            font-size: 17px;
                            font-weight: lighter;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 4;
                        }

                        .link {
                            .paragraph {
                                text-align: left;

                                .btn-course {
                                    outline: none;
                                    position: relative;
                                    font-weight: bold;
                                    background-color: white;
                                    margin-left: 0px;
                                    border-width: 2px !important;
                                    text-transform: none;
                                    font-size: 18px;
                                    color: black;
                                    top: 25px;
                                    letter-spacing: 0px;
                                    padding-left: 30px;
                                    padding-right: 30px;

                                    @media screen and (max-width:992px) {
                                        overflow: hidden;
                                        text-overflow: ellipsis;
                                        display: -webkit-box;
                                        -webkit-line-clamp: 1;
                                        /* number of lines to show */
                                        -webkit-box-orient: vertical;
                                    }
                                }
                            }
                        }
                    }
                }
            }

        }
    }
}